import { Component, OnInit } from '@angular/core';
import { SuperStockistService } from '../service/super-stockist.service';
import { SuperStockist } from '../model/SuperStockist';
import { BusinessAddress } from '../model/BusinessAddress';
import { BusinessContactInformation } from '../model/BusinessContactInformation';
import { PersonalContactInformation } from '../model/PersonalContactInformation';
import {State} from '../model/State';
import {Dist} from '../model/Dist';
import {AuthService} from '../service/auth.service';

import {StateService} from '../service/state.service';

import { isUndefined } from 'util';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-super-stockist-edit',
  templateUrl: './super-stockist-edit.component.html',
  styleUrls: ['./super-stockist-edit.component.css']
})
export class SuperStockistEditComponent implements OnInit {

  id : string;
  superStockist : SuperStockist;
  states : State[];
  dists : Dist[];

  constructor(private superStockistService: SuperStockistService, private AuthService : AuthService, private stateService : StateService,private router: Router, private route: ActivatedRoute) {
    this.superStockist= new SuperStockist();
    this.superStockist.businessAddress= new BusinessAddress();
    this.superStockist.businessContactInformation = new BusinessContactInformation();
    this.superStockist.personalContactInformation= new PersonalContactInformation();
    this.ChkForAuth();
    this.stateService.getStates().subscribe(data=>{
      this.states=data
    },(data)=>{
      console.log('Error getting the state' + data);
    }).add(()=>{
      route.paramMap.subscribe(p=>{
        this.id=p.get('id');
        this.getData();
      })
    })    
   }

  ngOnInit() {
  }

  private ChkForAuth() {
    var roleId = this.AuthService.getRoleId();
    if (roleId != "1") {
      alert('You are not authorized to access the resurce');
      this.router.navigate(['../home']);
    }
  }





  getData(){
    this.superStockistService.getData(this.id).subscribe(
      (data)=>{       
        
        this.superStockist=data;      
        console.log('Data from server');
        console.log(this.superStockist);
        console.log(data);  
        
        this.getDistrict();
      })
  }
  

  SaveRecord(){
    if(this.validateRecord()==false){
      return false;
    }else{
      this.superStockist.parentCompanyId=this.AuthService.getParentCompanyId();    
      this.superStockistService.putData(this.superStockist).add((data)=>{   
        alert('Record Saved')  
        this.router.navigate(['../SuperStockistList']);
    });    
    }    
  }




  onStateChange(args){    
    this.superStockist.businessAddress.state=args.target.value;
    this.getDistrict();
    this.superStockist.businessAddress.district=this.dists[0].Name;
  }




  getDistrict(){
    var stateName = this.superStockist.businessAddress.state;
     this.dists = this.states.find(o=>o.Name==stateName).Dist;    
  }







  validateRecord() : boolean{    
    alert(this.superStockist.fname)
    if((isUndefined(this.superStockist.fname)) || this.superStockist.fname.trim()==''){
      alert('First name is required.');
      return false;
    }

    if((isUndefined(this.superStockist.lname)) || this.superStockist.lname.trim()==''){
      alert('Last name is required.');
      return false;
    }

    if((isUndefined(this.superStockist.businessContactInformation.email)) || this.superStockist.businessContactInformation.email==''){
      alert('Email is required.');
      return false;
    }    
  }



}
