import { user } from './user';
import { BusinessAddress } from './BusinessAddress';
import {Distributor} from './Distributor';

export class SuperStockist extends user
{
    private Id : string;
    public get ID() : string {
        return this.Id;
    }
    public set ID(v : string) {
        this.Id = v;
    }


    private BusinessAddress: BusinessAddress;
    public get businessAddress(): BusinessAddress {
        return this.BusinessAddress;
    }
    public set businessAddress(value: BusinessAddress) {
        this.BusinessAddress = value;
    }


    
    private ParentCompanyId : string;
    public get parentCompanyId() : string {
        return this.ParentCompanyId;
    }
    public set parentCompanyId(v : string) {
        this.ParentCompanyId = v;
    }


    
    private _distributors : Distributor[];
    public get distributors() : Distributor[] {
        return this._distributors;
    }
    public set distributors(v : Distributor[]) {
        this._distributors = v;
    }
    




    
}