import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HomeCompomentComponent} from './home-compoment/home-compoment.component';
import {AboutCompomentComponent} from './about-compoment/about-compoment.component';
import {TranAssignStockComponent} from './Component/tran-assign-stock/tran-assign-stock.component';

import {SuperStockistListComponent} from './super-stockist-list/super-stockist-list.component';
import {SuperStockistComponent} from './super-stockist/super-stockist.component';
import {SuperStockistEditComponent} from './super-stockist-edit/super-stockist-edit.component';

import {DistributorListComponent} from './distributor-list/distributor-list.component';
import {DistributorComponent} from './distributor/distributor.component';
import {DistributorEditComponent} from './distributor-edit/distributor-edit.component';

import {RetailerListComponent} from './retailer-list/retailer-list.component';
import {RetailerComponent} from './retailer/retailer.component';
import {RetailerEditComponent} from './retailer-edit/retailer-edit.component';

import {BusinessAssociateComponent} from './business-associate/business-associate.component';

import { StockListComponent } from './Component/stock-list/stock-list.component';

import {LoginComponent} from './login/login.component';
import {AuthGuard} from './auth.guard'

const routes: Routes = [
  {path: '', pathMatch: 'full', redirectTo: 'login'},
  {path :'login', component :LoginComponent},
  {path :'home', component :HomeCompomentComponent},
  {path :'about', component :AboutCompomentComponent},
  {path :'Transaction', component :TranAssignStockComponent},

  {path :'SuperStockistList', component :SuperStockistListComponent,canActivate:[AuthGuard]},
  {path :'superStockistAdd', component :SuperStockistComponent},
  {path :'superStockistEdit/:id', component :SuperStockistEditComponent},

  {path :'distributorList', component :DistributorListComponent},
  {path :'distributorAdd', component :DistributorComponent},
  {path :'distributorEdit/:id', component :DistributorEditComponent},

  {path :'retailerList', component :RetailerListComponent},
  {path :'retailerAdd', component :RetailerComponent},
  {path :'retailerEdit/:id', component :RetailerEditComponent},

  {path :'BusinessAssociate', component :BusinessAssociateComponent},

  {path :'Stock', component :StockListComponent},

  
  

  

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
