import { Component, OnInit } from '@angular/core';
import { RetailerService } from '../service/retailer.service';
import { Retailer } from '../model/Retailer';
import { BusinessAddress } from '../model/BusinessAddress';
import { BusinessContactInformation } from '../model/BusinessContactInformation';
import { PersonalContactInformation } from '../model/PersonalContactInformation';
import {State} from '../model/State';
import {Dist} from '../model/Dist';
import {AuthService} from '../service/auth.service';
import {StateService} from '../service/state.service';

import { isUndefined } from 'util';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-retailer',
  templateUrl: './retailer.component.html',
  styleUrls: ['./retailer.component.css']
})
export class RetailerComponent implements OnInit {

 
  obj : Retailer;
  states : State[];
  dists : Dist[];

  constructor(private retailerService : RetailerService,private AuthService : AuthService, private stateService : StateService,private router: Router, private route: ActivatedRoute) 
  { 
    this.ChkForAuth();
    
    this.obj= new Retailer();
    this.obj.businessAddress= new BusinessAddress();
    this.obj.businessContactInformation = new BusinessContactInformation();
    this.obj.personalContactInformation= new PersonalContactInformation();

    this.stateService.getStates().subscribe(data=>{
      this.states=data;
      this.obj.businessAddress.state=this.states[0].Name;      
      this.getDistrict()
    },(data)=>{
      console.log('Error getting the state' + data);
    })
  }

  ngOnInit() {
  }


  private ChkForAuth() {
    var roleId = this.AuthService.getRoleId();
    if (roleId != "3") {
      alert('You are not authorized to access the resurce');
      this.router.navigate(['../home']);
    }
  }

  onStateChange(args){    
    this.obj.businessAddress.state=args.target.value;
    this.getDistrict();
  }

  getDistrict(){
    var stateName = this.obj.businessAddress.state;
     this.dists = this.states.find(o=>o.Name==stateName).Dist;    
     this.obj.businessAddress.district=this.dists[0].Name;
  }

  validateRecord() : boolean{    
    if((isUndefined(this.obj.fname)) || this.obj.fname.trim()==''){
      alert('First name is required.');
      return false;
    }

    if((isUndefined(this.obj.lname)) || this.obj.lname.trim()==''){
      alert('Last name is required.');
      return false;
    }

    if((isUndefined(this.obj.email)) || this.obj.email==''){
      alert('Email is required.');
      return false;
    }
    
  }

  SaveRecord(){
    if(this.validateRecord()==false){
      return false;
    }else{           
      this.obj.distributorId=this.AuthService.getId();  
          
     this.retailerService.postData(this.obj).add((data)=>{   
        alert('Record Saved')  
        this.router.navigate(['../retailerList']);
    });   
    }    
  }





}
