import { Component, OnInit } from '@angular/core';
import { DistributorService } from '../service/distributor.service';
import { Distributor } from '../model/Distributor';

import {AuthService} from '../service/auth.service';

import { AppSettingsService } from '../service/app-settings.service';
import { MyAppSetting } from '../config/MyAppSetting';
import {AppStorageService} from '../service/app-storage.service';
import { Router } from '@angular/router';
import { FilterCriteria } from '../model/FilterCriteria';

@Component({
  selector: 'app-distributor-list',
  templateUrl: './distributor-list.component.html',
  styleUrls: ['./distributor-list.component.css']
})
export class DistributorListComponent implements OnInit {

 
  objCollection: Distributor[];
  filterCriteria : FilterCriteria;

  constructor(private DistributorService: DistributorService, private AuthService : AuthService,private router: Router ) { 
        this.ChkForAuth();
        this.filterCriteria=new FilterCriteria();
  }  

  ngOnInit() {
  }


  private ChkForAuth() {
    var roleId = this.AuthService.getRoleId();
    if (roleId != "2") {
      alert('You are not authorized to access the resurce');
      this.router.navigate(['../home']);
    }
  }



  loadData() {
    this.filterCriteria=new FilterCriteria();
    var superStockistId = this.AuthService.getId();
    this.DistributorService.getDataAll(superStockistId)
      .subscribe(data => {         
        this.objCollection = data as Distributor[];
        (err) => { console.log(err) };
      });
  }

}
