import { BusinessContactInformation } from './BusinessContactInformation';
import { PersonalContactInformation } from './PersonalContactInformation';


export class user
{
    
   /**
    *
    */
   constructor() {      

   }
   id:string;
   name:string;
   private Fname : string;
   public get fname() : string {
       return this.Fname;
   }
   public set fname(v : string) {
       this.Fname = v;
   }
   


   private Lname : string;
   public get lname() : string {
       return this.Lname;
   }
   public set lname(v : string) {
       this.Lname = v;
   }


  
  private Email : string;
  public get email() : string {
      return this.Email;
  }
  public set email(v : string) {
      this.Email = v;
  }

  
  private Password : string;
  public get password() : string {
      return this.Password;
  }
  public set password(v : string) {
      this.Password = v;
  }
  
  
  private RoleId : number;
  public get roleId() : number {
      return this.RoleId;
  }
  public set roleId(v : number) {
      this.RoleId = v;
  }
  
   

   
   private BusinessContactInformation : BusinessContactInformation;
   public get businessContactInformation() : BusinessContactInformation {
       return this.BusinessContactInformation;
   }
   public set businessContactInformation(v : BusinessContactInformation) {
       this.BusinessContactInformation = v;
   }
   


   
   private PersonalContactInformation : PersonalContactInformation;
   public get personalContactInformation() : PersonalContactInformation {
       return this.PersonalContactInformation;
   }
   public set personalContactInformation(v : PersonalContactInformation) {
       this.PersonalContactInformation = v;
   }   
   
    
}