import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { HttpHeaders } from '@angular/common/http';

import { Retailer } from '../model/Retailer'
import { AppSettingsService } from '../service/app-settings.service';
import { MyAppSetting } from '../config/MyAppSetting';

@Injectable({
  providedIn: 'root'
})

export class RetailerService {

  myAppSetting: MyAppSetting;
  constructor(private myHttpclient: HttpClient, private appSettingsService: AppSettingsService) 
  {
    appSettingsService.getSettings().subscribe(data => this.myAppSetting = data);
  }




  getData(id: string) : Observable<Retailer> {
    var url = this.myAppSetting.baseUrl;
    url = url + '/Retailer/' + id;
    return this.myHttpclient.get(url) as Observable<Retailer>;
   }


  getDataAll(distributorId: string) {
    var url = this.myAppSetting.baseUrl;   
    url = url + '/Retailer/' + distributorId + '/0';
    return this.myHttpclient.get(url)     
  }


  postData(retailer: Retailer) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };

   
    var url = this.myAppSetting.baseUrl;
    url = url + '/Retailer';

    return this.myHttpclient.post<Retailer>(url, retailer, httpOptions).subscribe(
      data => {
        console.log("POST Request is successful ", data);
        return data;
      },
      error => {
        console.log("Error", error);
      });
  }




  putData(retailer: Retailer) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };

    var url = this.myAppSetting.baseUrl;
    url = url + '/Retailer';

    return this.myHttpclient.put<Retailer>(url ,retailer, httpOptions).subscribe(
      data => {
        console.log("PUT Request is successful ", data);
      },
      error => {
        console.log("Error", error);
      });
  }

}
