export class BusinessAddress
{

    private Id: string;
    public get ID(): string {
        return this.Id;
    }
    public set ID(value: string) {
        this.Id = value;
    }


    private Country: string;
    public get country(): string {
        return this.Country;
    }
    public set country(value: string) {
        this.Country = value;
    }


    private State: string;
    public get state(): string {
        return this.State;
    }
    public set state(value: string) {
        this.State = value;
    }

    private District: string;
    public get district(): string {
        return this.District;
    }
    public set district(value: string) {
        this.District =value;
    }



    private Region: string;
    public get region(): string {
        return this.Region;
    }
    public set region(value: string) {
        this.Region = value;
    }



    private Pin: string;
    public get pin(): string {
        return this.Pin;
    }
    public set pin(value: string) {
        this.Pin = value;
    }



    private OutLateName :string;
    public get outLateName(): string {
        return this.OutLateName;
    }
    public set outLateName(value: string) {
        this.OutLateName = value;
    }



}