import { Pipe, PipeTransform } from '@angular/core';
import {user} from '../model/user';

@Pipe({
  name: 'filterByUserRoleTypePipe'
})
export class FilterByUserRoleTypePipePipe implements PipeTransform {

  transform(users: user[],userRoleType: string,fiterData :boolean): user[] {   
   
  
    if(fiterData){
      var result = users.filter(u=>u["roleId"].toString()==userRoleType);
      return result;
    }
    else{
      return users;
    }
    
  }

}
