export class BusinessContactInformation
{
    
    private Id: string;
    public get ID(): string {
        return this.Id;
    }
    public set ID(value: string) {
        this.Id = value;
    }



    private _email: string;
    public get email(): string {
        return this._email;
    }
    public set email(value: string) {
        this._email = value;
    }



    private MobNoPrimary: string;
    public get mobNoPrimary(): string {
        return this.MobNoPrimary;
    }
    public set mobNoPrimary(value: string) {
        this.MobNoPrimary = value;
    }

    

    private MobNoAlternate: string;
    public get mobNoAlternate(): string {
        return this.MobNoAlternate;
    }
    public set mobNoAlternate(value: string) {
        this.MobNoAlternate = value;
    }

    
}