import { BusinessAddress } from './BusinessAddress';
import { BusinessAssociate } from './BusinessAssociate';
import { user } from './user';

export class Retailer extends user
{
    private Id : string;
    public get ID() : string {
        return this.Id;
    }
    public set ID(v : string) {
        this.Id = v;
    }

    
    private BusinessAddress: BusinessAddress;
    public get businessAddress(): BusinessAddress {
        return this.BusinessAddress;
    }
    public set businessAddress(value: BusinessAddress) {
        this.BusinessAddress = value;
    }
    

    private BusinessAssociate : BusinessAssociate[];
    public get businessAssociate() {
        return this.BusinessAssociate;
    }
    public set businessAssociate(value) {
        this.BusinessAssociate = value;
    }
        

    private DistributorId: string;
    public get distributorId(): string {
        return this.DistributorId;
    }
    public set distributorId(value: string) {
        this.DistributorId = value;
    }    
    
}