import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry, map, mapTo } from 'rxjs/operators';

import { HttpHeaders } from '@angular/common/http';

import { SuperStockist } from '../model/SuperStockist'
import { AppSettingsService } from '../service/app-settings.service';
import { MyAppSetting } from '../config/MyAppSetting';

@Injectable({
  providedIn: 'root'
})
export class SuperStockistService {

  myAppSetting: MyAppSetting;
  constructor(private myHttpclient: HttpClient, private appSettingsService: AppSettingsService) {
    appSettingsService.getSettings().subscribe(data => this.myAppSetting = data);
  }


  getData(id: string) : Observable<SuperStockist> {
    var url = this.myAppSetting.baseUrl;
    url = url + '/SuperStockist/' + id;
    return this.myHttpclient.get(url) as Observable<SuperStockist>;    

  }


  getDataAll(parentCompanyId: string) {
    var url = this.myAppSetting.baseUrl;
    //var url = 'https://localhost:5001/api';
    url = url + '/SuperStockist/' + parentCompanyId + '/0';

    return this.myHttpclient.get(url)
      /*.subscribe(
        {
          next: (data) => {
            return Observable.apply(data) ;
          },
          error: (err) => {
            console.log('An error...')
            console.log(err);
          },
          complete: () => { console.log('call complete') }
        });*/
  }



  postData(superStockist: SuperStockist) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };

    //var url = this.myAppSetting.baseUrl;
    var url = this.myAppSetting.baseUrl;
    url = url + '/SuperStockist';

    return this.myHttpclient.post<SuperStockist>(url, superStockist, httpOptions).subscribe(
      data => {
        console.log("POST Request is successful ", data);
        return data;
      },
      error => {
        console.log("Error", error);
      });
  }




  putData(superStockist: SuperStockist) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };

    var url = this.myAppSetting.baseUrl;
    url = url + '/SuperStockist';

    return this.myHttpclient.put<SuperStockist>(url ,superStockist, httpOptions).subscribe(
      data => {
        console.log("PUT Request is successful ", data);
      },
      error => {
        console.log("Error", error);
      });
  }



}
