import { Pipe, PipeTransform, ComponentFactoryResolver } from '@angular/core';
import { isString, isNumber, isObject, isUndefined } from 'util';


@Pipe({
  name: 'filterObjArrayByMatchProperty',
  pure: false
})



export class FilterObjArrayByMatchPropertyPipe implements PipeTransform {

  transform(items: any[], criteria: Object, propname: string): any {  
    //console.log(items);
    //console.log(criteria);  
    var result: any[] = [];
    if (!items || !criteria) {
      return items;
    }

    var hasNoFilter  = true;
    var props = Object.keys(criteria);
    props.forEach(p => {
      if(!isUndefined(criteria[p]) && (criteria[p]!='')){
        hasNoFilter=false;
      }
    });

    if(hasNoFilter){
      return items;
    }



    if (propname != '') {
      result = items.filter(item => item[propname].indexOf(criteria[propname]) !== -1);
    }
    else {
       result = this.filterByCriteriaObject(items, criteria, result);
    }
    return result;
  }


  private filterByCriteriaObject(items: any[], criteria: Object, result: any[]) : any[] {
    var props = Object.keys(items[0]);
    items.forEach(i => {
      var itemFound = true;
      props.forEach(prop => {
        itemFound = this.filterByStringcDataValue(i, prop, criteria, itemFound);
        itemFound = this.filterByNumericDataValue(i, prop, criteria, itemFound);       
      });
      if (itemFound) {
        result.push(i);
      }
    });
    return result;
  }

  private filterByStringcDataValue(i: any, prop: string, criteria: Object, itemFound: boolean) {
    if (isString(i[prop])) {
      var propValue = i[prop];
      if ((!isUndefined(criteria[prop])) && (criteria[prop] != '')) {
        if (propValue.indexOf(criteria[prop]) == -1) {
          itemFound = false;
        }
      }
    }
    return itemFound;
  }

  private filterByNumericDataValue(i: any, prop: string, criteria: Object, itemFound: boolean) {
    if (isNumber(i[prop])) {
      var propValue = "" + i[prop];
      if ((!isUndefined(criteria[prop])) && (criteria[prop] != '')) {
        if (propValue.indexOf(criteria[prop]) == -1) {
          itemFound = false;
        }
      }
    }
    return itemFound;
  }
}
