import { Injectable,Inject } from '@angular/core';
import {LOCAL_STORAGE, WebStorageService} from 'angular-webstorage-service';
import {AppStorage} from '../model/app-storage';

@Injectable({
  providedIn: 'root'
})
export class AppStorageService {

  constructor(@Inject(LOCAL_STORAGE) private storage: WebStorageService, private appStoragea :AppStorage) {    
     
   } 

   setUserNameInLocal(value : string){
    this.appStoragea.UserName=value;
   }

   getGetNameInLocal():string{
     return this.appStoragea.UserName;
  }

  setParentCompanyIdInLocal(value : string){
    this.appStoragea.ParentCompanyId=value;
   }

   getParentCompanyIdInLocal():string{
     return this.appStoragea.ParentCompanyId;
  }
}
