import { Pipe, PipeTransform } from '@angular/core';


@Pipe({
  name: 'filterByMatchText',
  pure:false
})
export class FilterByMatchTextPipe implements PipeTransform {

  transform(items: any[], criteria: Object, propname : string): any {   
       if (!items || !criteria) {
        return items;
       }    
    
    var result = items.filter(item => item[propname].indexOf(criteria) !== -1);
    console.log(result)
    return result;
}

}
