import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { HttpHeaders } from '@angular/common/http';

import { MyAppSetting } from '../config/MyAppSetting';
import { AppSettingsService } from '../service/app-settings.service';
import {Stock} from '../model/stock';

@Injectable({
  providedIn: 'root'
})
export class StockService {

  myAppSetting: MyAppSetting;
  constructor(private myHttpclient: HttpClient, private appSettingsService: AppSettingsService) { 
    appSettingsService.getSettings().subscribe(data => this.myAppSetting = data);
  }


  getbyParentCompnayAllData(id: string) : Observable<Stock[]> {    
    var url = this.myAppSetting.baseUrl;
    url = url + '/Stock/' + id + '/0'
    return this.myHttpclient.get(url) as Observable<Stock[]>;    

  }


  getbyParentCompnay(id: string) : Observable<Stock[]> {
    var url = this.myAppSetting.baseUrl;
    url = url + '/Stock/' + id + '/1'
    return this.myHttpclient.get(url) as Observable<Stock[]>;    

  }


  getbySuperStokist(id: string) : Observable<Stock[]> {
    var url = this.myAppSetting.baseUrl;
    url = url + '/Stock/' + id + '/2'
    return this.myHttpclient.get(url) as Observable<Stock[]>;    

  }


  getbyDistributor(id: string) : Observable<Stock[]> {
    var url = this.myAppSetting.baseUrl;
    url = url + '/Stock/' + id + '/3'
    return this.myHttpclient.get(url) as Observable<Stock[]>;    

  }



  getbyRetailer(id: string) : Observable<Stock[]> {
    var url = this.myAppSetting.baseUrl;
    url = url + '/Stock/' + id + '/4'
    return this.myHttpclient.get(url) as Observable<Stock[]>;    

  }


  getbyBusinessAssociate(id: string) : Observable<Stock[]> {
    var url = this.myAppSetting.baseUrl;
    url = url + '/Stock/' + id + '/5'
    return this.myHttpclient.get(url) as Observable<Stock[]>;    

  }

  stockTransfer(stocks :Stock[]) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    
     var url = this.myAppSetting.baseUrl;
     url = url + '/Stock/?v=1';
 
     return this.myHttpclient.post<Stock>(url, stocks, httpOptions).subscribe(
       data => {
         console.log("POST Request is successful ", data);
         return data;
       },
       error => {
         console.log("Error", error);
       });

  };


  sendActivationReq(stocks :Stock[]) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    
     var url = this.myAppSetting.baseUrl;
     url = url + '/Stock/?v=2';
 
     return this.myHttpclient.post<Stock>(url, stocks, httpOptions).subscribe(
       data => {
         console.log("POST Request is successful ", data);
         return data;
       },
       error => {
         console.log("Error", error);
       });

  };

  

  doActivation(stocks :Stock[]) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    
     var url = this.myAppSetting.baseUrl;
     url = url + '/Stock/?v=3';
 
     return this.myHttpclient.post<Stock>(url, stocks, httpOptions).subscribe(
       data => {
         console.log("POST Request is successful ", data);
         return data;
       },
       error => {
         console.log("Error", error);
       });

  };




  
}
