import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'deviceActivationStateType'
})
export class DeviceActivationStateTypePipe implements PipeTransform {

  transform(value: string, retValue: string): string {
    retValue=value;
    if(value=='0')
    retValue='Not Active'

    if(value=='1')
    retValue='Active'

    if(value=='2')
    retValue='In Progress'

    return retValue;
  }

}
