import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry, map, mapTo } from 'rxjs/operators';

import { HttpHeaders } from '@angular/common/http';

import { Distributor } from '../model/Distributor'
import { AppSettingsService } from '../service/app-settings.service';
import { MyAppSetting } from '../config/MyAppSetting';

@Injectable({
  providedIn: 'root'
})
export class DistributorService {

  myAppSetting: MyAppSetting;
  constructor(private myHttpclient: HttpClient, private appSettingsService: AppSettingsService) 
  {
    appSettingsService.getSettings().subscribe(data => this.myAppSetting = data);
  }




  getData(id: string) : Observable<Distributor> {
    var url = this.myAppSetting.baseUrl;
    url = url + '/Distributor/' + id;
    return this.myHttpclient.get(url) as Observable<Distributor>;    

  }


  getDataAll(superStokictId: string) {
    var url = this.myAppSetting.baseUrl;   
    url = url + '/Distributor/' + superStokictId + '/0';
    return this.myHttpclient.get(url)     
  }



  postData(distributor: Distributor) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
   
    var url = this.myAppSetting.baseUrl;
    url = url + '/Distributor';

    return this.myHttpclient.post<Distributor>(url, distributor, httpOptions).subscribe(
      data => {
        console.log("POST Request is successful ", data);
        return data;
      },
      error => {
        console.log("Error", error);
      });
  }




  putData(distributor: Distributor) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };

    var url = this.myAppSetting.baseUrl;
    url = url + '/Distributor';

    return this.myHttpclient.put<Distributor>(url ,distributor, httpOptions).subscribe(
      data => {
        console.log("PUT Request is successful ", data);
      },
      error => {
        console.log("Error", error);
      });
  }





}
