import { Component, OnInit } from '@angular/core';
import { SuperStockistService } from '../service/super-stockist.service';
import { SuperStockist } from '../model/SuperStockist';

import {AuthService} from '../service/auth.service';

import { AppSettingsService } from '../service/app-settings.service';
import { MyAppSetting } from '../config/MyAppSetting';
import {AppStorageService} from '../service/app-storage.service';
import { Router } from '@angular/router';
import { FilterCriteria } from '../model/FilterCriteria';


@Component({
  selector: 'app-super-stockist-list',
  templateUrl: './super-stockist-list.component.html',
  styleUrls: ['./super-stockist-list.component.css']
})
export class SuperStockistListComponent implements OnInit {
  
  superStockists: SuperStockist[];
  filterCriteria : FilterCriteria;
  
  

  constructor(private superStockistService: SuperStockistService,private AuthService : AuthService,private router: Router )
  {     
    this.ChkForAuth();
    this.filterCriteria=new FilterCriteria();
    //this.filterCriteria.Fname='';
    //this.filterCriteria.Lname='';
    //this.filterCriteria.Email='';
  }

  ngOnInit() { 
    
   }

   private ChkForAuth() {
    var roleId = this.AuthService.getRoleId();
    if (roleId != "1") {
      alert('You are not authorized to access the resurce');
      this.router.navigate(['../home']);
    }
  }

  loadData() {
    this.filterCriteria=new FilterCriteria();
    //this.filterCriteria.Fname='';
    //this.filterCriteria.Lname='';
    //this.filterCriteria.Email='';
    
    var parentCompanyId=this.AuthService.getParentCompanyId();
    console.log(parentCompanyId)
    this.superStockistService.getDataAll(parentCompanyId)
      .subscribe(data => {        
        this.superStockists = data as SuperStockist[];
        (err) => { console.log(err) };
      });
  }
}

