import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Pipe, PipeTransform } from '@angular/core';


import {AuthService} from '../../service/auth.service';

import {Stock} from '../../model/stock';
import {StockService} from '../../service/stock.service';

import { SuperStockistService } from '../../service/super-stockist.service';
import { SuperStockist } from '../../model/SuperStockist';

import {DistributorService} from '../../service/distributor.service';
import { Distributor } from '../../model/Distributor'; 

import {RetailerService} from '../../service/retailer.service';
import { Retailer } from '../../model/Retailer'; 

import {BusinessAssociateService} from '../../service/business-associate.service';
import { BusinessAssociate } from '../../model/BusinessAssociate'; 

import {ParentCompanyService} from '../../service/parent-company.service'

import { user } from '../../model/user';

import {FilterByUserRoleTypePipePipe} from '../../filter/filter-by-user-role-type-pipe.pipe'


@Component({
  selector: 'app-tran-assign-stock',
  templateUrl: './tran-assign-stock.component.html',
  styleUrls: ['./tran-assign-stock.component.css']
})
export class TranAssignStockComponent implements OnInit {

  stocks: Stock[]; 
  filterCriteria :filterCriteria;
  roleId : string;
  id :string;
  userType: object;
  userTypes:object[];
  users:user[];
  loginUserName : string;
  stockTransUserId:string;
  filterData : boolean=false;
  

  constructor(private stockService: StockService, private parentCompanyService : ParentCompanyService, private superStockistService: SuperStockistService, private distributorService : DistributorService, 
    private retailerService : RetailerService, private BusinessAssociateService:BusinessAssociateService, private  AuthService : AuthService,private router: Router) { 
    this.ChkForAuth();     
    this.filterCriteria = new filterCriteria();
  }

  ngOnInit() {
   this.userTypes= [];
   if(this.roleId=="1"){   
   this.userTypes.push({'name':'Super Stokist' , 'id' : "2"});
   this.userTypes.push({'name':'Distributor' , 'id' : "3"});
   this.userTypes.push({'name':'Retailer' , 'id' : "4"});
   this.userTypes.push({'name':'Business Associate' , 'id' : "5"});   
   this.userType ={'name':'Super Stokist' , 'id' : "2"};
   this.filterData=true;
   }

   if(this.roleId=="2"){
    this.userTypes.push({'name':'Distributor' , 'id' : "3"});  
    this.userType =this.userTypes[0];
    this.filterData=false;
   }

   if(this.roleId=="3"){
    this.userTypes.push({'name':'Retailer' , 'id' : "4"});  
    this.userTypes.push({'name':'Business Associate' , 'id' : "5"}); 
    this.userType =this.userTypes[0];
    this.filterData=false;
   }
   

   this.loginUserName = this.AuthService.getEmail();
   this.users=[];
 
  }

  private ChkForAuth() {
    this.roleId = this.AuthService.getRoleId();
    if(this.roleId=="1"){
      this.id = this.AuthService.getParentCompanyId();
    }else{
      this.id = this.AuthService.getId();
    }
    this.id = this.AuthService.getId();
    if ((this.roleId != "1") && (this.roleId != "2")  && (this.roleId != "3") ) {
      alert('You are not authorized to access the resurce');
      this.router.navigate(['../home']);
    }
  }


  loadData() {    
    
    this.filterCriteria = new filterCriteria();

    if(this.roleId=="1"){
      this.getbyParentCompnay();
      //this.userType ={'name':'Super Stokist' , 'id' : "2"};
    }
   

    if(this.roleId=="2"){
      this.getbySuperStokist();
      this.userType ={'name':'Distributor' , 'id' : "3"};
    }
    

    if(this.roleId=="3")
    {
      this.getbyDistributor(); 
      //this.userType ={'name':'Retailer' , 'id' : "3"};
    }
     
    
    this.loadUser();

    

  }

  private getbyParentCompnay() {
    var parentCompanyId = this.AuthService.getParentCompanyId();    
    this.stockService.getbyParentCompnay(parentCompanyId)
      .subscribe(data => {
        this.stocks = data;       
        (err) => { console.log(err); };
      }).add(this.dataTransfornation());
  }


  private getbySuperStokist() {    
    this.stockService.getbySuperStokist(this.id)
      .subscribe(data => {
        this.stocks = data;
        (err) => { console.log(err); };
      }).add(this.dataTransfornation());
  }


  private getbyDistributor() {
    var id = this.AuthService.getId();
    this.stockService.getbyDistributor(id)
      .subscribe(data => {
        this.stocks = data;
        (err) => { console.log(err); };
      }).add(this.dataTransfornation());
  }


  private dataTransfornation() {
    return () => {
      this.stocks.forEach(e => {
        e.stockSelect=false;
        if (e.activatinonState = '0') {
          e.activatinonState = 'Active';
        }
        else {
          e.activatinonState = 'Not Active';
        }
      });
    };
  }


  private loadUser(){
    this.users=[];
    if(this.roleId=="1")
    {
      var id = this.AuthService.getParentCompanyId();     
      this.parentCompanyService.getCompanyUser(id).subscribe(data=>{      
        this.users=data;           
      }).add(()=>{
          if(this.users.length>0){
            //this.stockTransUserId=this.users[0].id; 
            this.stockTransUserId="0";         
          }     
      });
    } 


    if(this.roleId=="2")
    {
      var id = this.AuthService.getId();      
      this.distributorService.getDataAll(id).subscribe(data=>{   
      var distributors = data as Distributor[];   
      distributors.forEach(element => {
      var obj = new user();
      obj.id=element.id;
      obj.name=element.fname + element.lname;
      obj.roleId=element.roleId;      
      this.users.push(obj)   
       });
      }).add(()=>{        
            //this.stockTransUserId=this.users[0].id; 
            this.stockTransUserId="0";          
      });
    } 


    if(this.roleId=="3")
    {
      var id = this.AuthService.getId();  
      if(this.userType["id"]=="4"){
        this.retailerService.getDataAll(id).subscribe(data=>{   
          var retailers = data as Retailer[];   
          retailers.forEach(element => {
          var obj = new user();
          obj.id=element.id;
          obj.name=element.fname + element.lname;
          obj.roleId=element.roleId;      
          this.users.push(obj)   
           });
          }).add(()=>{       
                this.stockTransUserId="0";          
          });
      }   
      
      if(this.userType["id"]=="5"){
        this.users=[];
      }    
     
    } 

  }

  private selectAll(event){
  this.stocks.forEach(element => {
    element.stockSelect= event.target.checked;
  });
  }


  private onSelect(event,obj){
    obj.stockSelect= event.target.checked;
  }

   transferData(){    
     if(this.stockTransUserId=="0"){
       alert('Please select a user.')
       return;
     }
    var stocksTransfer: Stock[]=[];    
    this.stocks.forEach(element => {      
      if(element.stockSelect){
        stocksTransfer.push(element);
      }
    });

    stocksTransfer.forEach(element => {
     element.updatedBy=this.loginUserName;
     element.belongsToId=this.stockTransUserId;
     element.belongsTo=this.userType['id'];
    });
   
    this.stockService.stockTransfer(stocksTransfer).add(()=>{
      this.loadData();
    });
    
  }

  onUserTypeChange(args){
    console.log(args.target.value)
    this.userType['id']=args.target.value;    
  }

  private onUserChange(args){  
   console.log(this.stockTransUserId)
  }

}



class filterCriteria{
 
  constructor() {  
   
    
  } 
  
  activatinonState:''
  deviceName:''
      deviceId:''
      simNo:''     
      simProvider:''   
  
}
