import { Component, OnInit } from '@angular/core';
import { RetailerService } from '../service/retailer.service';
import { Retailer } from '../model/Retailer';

import {AuthService} from '../service/auth.service';
import { Router } from '@angular/router';
import { FilterCriteria } from '../model/FilterCriteria';

@Component({
  selector: 'app-retailer-list',
  templateUrl: './retailer-list.component.html',
  styleUrls: ['./retailer-list.component.css']
})
export class RetailerListComponent implements OnInit {

  objCollection: Retailer[];
  filterCriteria : FilterCriteria;

  constructor(private RetailerService: RetailerService, private AuthService : AuthService,private router: Router ) { 
        this.ChkForAuth();
        this.filterCriteria=new FilterCriteria();
  }  

  ngOnInit() {
  }


  private ChkForAuth() {
    var roleId = this.AuthService.getRoleId();
    if (roleId != "3") {
      alert('You are not authorized to access the resurce');
      this.router.navigate(['../home']);
    }
  }

  loadData() {
    this.filterCriteria=new FilterCriteria(); 
    var retailerId = this.AuthService.getId();
    this.RetailerService.getDataAll(retailerId)
      .subscribe(data => {         
        this.objCollection = data as Retailer[];
        (err) => { console.log(err) };
      });
    }

}
